// ** Reactstrap Imports
import { Card, CardBody, Row, Col, Input, Form, Button } from 'reactstrap'
import Flatpickr from 'react-flatpickr'
import '@styles/react/libs/flatpickr/flatpickr.scss'
import { useForm, Controller } from "react-hook-form"
import { activeOrganizationid, orgUserId } from '@src/helper/sassHelper'
import * as yup from "yup"
import { yupResolver } from "@hookform/resolvers/yup"
import { useParams } from 'react-router-dom'
import { addEmployeeDetails } from './store/index'
import { useDispatch } from 'react-redux'
import { useEffect } from 'react'
import axios from '@src/configs/axios/axiosConfig'
const activeOrgId = activeOrganizationid()
const userId = orgUserId()

const JobInfo = (props) => {

  const { tabId, selectedUser } = props
  const dispatch = useDispatch()
  const { id } = useParams()

  const schema = yup.object().shape({
    createdBy: yup.string().default(userId),
    id: yup.string().default(id),
    organizationId: yup.string().default(activeOrgId),
    dateofhire: yup.string().default(''),
    hourlyrate: yup.string().default(''),
    salary: yup.string().default(''),
    updatedBy: yup.string().default(userId)
  })

  const { handleSubmit, control, reset } = useForm({
    resolver: yupResolver(schema),
    defaultValues: schema.cast()
  })

  const onSubmit = async (data) => {
    await dispatch(addEmployeeDetails(data))
  }

  const getEmployeeDet = () => {
    axios.post('/organizationusers/getjobinfo', { id })
      .then((res) => {
        reset({
          createdBy: userId,
          updatedBy: userId,
          id,
          organizationId: activeOrgId,
          dateofhire: res.data.organizationusers.dateofhire.length === 13 ? String(res.data.organizationusers.dateofhire / 1000) : res.data.organizationusers.dateofhire || '',
          hourlyrate: res.data.organizationusers.hourlyrate || '',
          salary: res.data.organizationusers.salary || ''
        })
      }).catch((err) => {
        console.log(err)
      })
  }

  useEffect(() => {
    if (tabId === 'teamjobinfo') {
      getEmployeeDet()
    }
  }, [tabId])

  return (
    <Card>
      <CardBody>
        <Form onSubmit={handleSubmit(onSubmit)} >
          <dl>
            <Row>
              <Col sm='3'>
                <dt>Job Title</dt>
              </Col>
              <Col sm='9'>
                <dd> {selectedUser.rolename || ''}</dd>
              </Col>
            </Row>
          </dl>
          <dl>
            <Row>
              <Col sm='3'>
                <dt>Date of Hire</dt>
              </Col>
              <Col sm='9'>
                <Controller
                  control={control}
                  name={`dateofhire`}
                  id='dateofhire'
                  render={({ field }) => (
                    <Flatpickr
                      value={field.value}
                      onChange={(date, dateStr) => { field.onChange(dateStr) }}
                      options={{ altInput: true, altFormat: "M j, Y", dateFormat: "U" }}
                      className='form-control invoice-edit-input date-picker'
                    />
                  )}
                />
              </Col>
            </Row>
          </dl>
          <dl>
            <Row>
              <Col sm='3'>
                <dt>Salary</dt>
              </Col>
              <Col sm='9'>
                <Controller
                  control={control}
                  name={`salary`}
                  id='salary'
                  render={({ field }) => (
                    <Input
                      {...field}
                      className='form-control invoice-edit-input date-picker'
                    />
                  )}
                />
              </Col>
            </Row>
          </dl>
          <dl>
            <Row>
              <Col sm='3'>
                <dt>Hourly Rate</dt>
              </Col>
              <Col sm='9'>
                <Controller
                  control={control}
                  name={`hourlyrate`}
                  id='hourlyrate'
                  render={({ field }) => (
                    <Input
                      {...field}
                      className='form-control invoice-edit-input date-picker'
                    />
                  )}
                />
              </Col>
            </Row>
          </dl>
          <dl>
            <Col className='justify-content-end'>
              <Button type='submit' color='primary'>Save</Button>
            </Col>
          </dl>
        </Form>
      </CardBody>
    </Card>
  )
}
export default JobInfo
