// ** React Imports
import { useEffect, useState, Fragment } from 'react'
import { useParams, Link } from 'react-router-dom'

// ** Store & Actions
import { getUser } from '../store'
import { useSelector, useDispatch } from 'react-redux'

// ** Reactstrap Imports
import { Row, Col, Alert } from 'reactstrap'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
// ** User View Components
import UserTabs from './Tabs'
import UserInfoCard from './UserInfoCard'

// ** Styles
import '@styles/react/apps/app-users.scss'

const TeamView = () => {
  // ** Store Vars
  const store = useSelector(state => state.users)
  const dispatch = useDispatch()

  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  // ** Hooks
  const { id } = useParams()

  // ** Get suer on mount
  useEffect(async () => {
    setBlock(true)
    await dispatch(getUser(id))
    setBlock(false)
  }, [dispatch])

  const [active, setActive] = useState('')

  useEffect(() => {
    if (store.selectedUser !== null && active === '') {
      setActive((store.selectedUser.usertype === '1') ? 'teamjobinfo' : 'mypermissions')
    }
  })

  const toggleTab = tab => {
    if (active !== tab) {
      setActive(tab)
    }
  }

  return store.selectedUser !== null && store.selectedUser !== undefined ? (
    <UILoader blocking={block} loader={<Loader />}>
      <div className='app-user-view'>
        <Row>
          <Col xl='4' lg='5' xs={{ order: 1 }} md={{ order: 0, size: 5 }}>
            <UserInfoCard selectedUser={store.selectedUser} />
          </Col>
          <Col xl='8' lg='7' xs={{ order: 0 }} md={{ order: 1, size: 7 }}>
            <UserTabs active={active} toggleTab={toggleTab} selectedUser={store.selectedUser} />
          </Col>
        </Row>
      </div>
    </UILoader>
  ) : (
    <UILoader blocking={block} loader={<Loader />}>
      <Alert color='danger'>
        <h4 className='alert-heading'>User not found</h4>
        <div className='alert-body'>
          User with id: {id} doesn't exist. Check list of all Users: <Link to='/apps/user/list'>Users List</Link>
        </div>
      </Alert>
    </UILoader>
  )
}
export default TeamView
