// ** React Imports
import { Fragment } from 'react'

// ** Reactstrap Imports
import { Nav, NavItem, NavLink, TabContent, TabPane } from 'reactstrap'

// ** Icons Imports
import { User, Lock, Bookmark, Bell, Link, CheckSquare, Edit2 } from 'react-feather'

// ** User Components
import Attendance from './Attendance'
import JobInfo from './JobInfo'
import Leave from './Leave'
import Permissions from './Permissions'

const UserTabs = ({ active, toggleTab, selectedUser }) => {
  return (
    <Fragment>
      <Nav pills className='mb-2'>
        {selectedUser.usertype === '1' &&
          <NavItem>
            <NavLink active={active === 'teamjobinfo'} onClick={() => toggleTab('teamjobinfo')}>
              <CheckSquare className='font-medium-3 me-50' />
              <span className='fw-bold'>Job Info</span>
            </NavLink>
          </NavItem>
        }
        <NavItem>
          <NavLink active={active === 'teammypermissions'} onClick={() => toggleTab('teammypermissions')}>
            <Edit2 className='font-medium-3 me-50' />
            <span className='fw-bold'>Permissions</span>
          </NavLink>
        </NavItem>
        {selectedUser.usertype === '1' &&
          <NavItem>
            <NavLink active={active === 'teamattendance'} onClick={() => toggleTab('teamattendance')}>
              <Bookmark className='font-medium-3 me-50' />
              <span className='fw-bold'>Attendance</span>
            </NavLink>
          </NavItem>
        }{selectedUser.usertype === '1' &&
          <NavItem>
            <NavLink active={active === 'teamleave'} onClick={() => toggleTab('teamleave')}>
              <Bell className='font-medium-3 me-50' />
              <span className='fw-bold'>Leave</span>
            </NavLink>
          </NavItem>
        }
      </Nav>
      <TabContent activeTab={active}>
        <TabPane tabId='teamjobinfo'>
          <JobInfo selectedUser={selectedUser} tabId={active} />
        </TabPane>
        <TabPane tabId='teammypermissions'>
          <Permissions roleId={selectedUser.roleid} tabId={active} />
        </TabPane>
        <TabPane tabId='teamattendance'>
          <Attendance tabId={active}/>
        </TabPane>
        <TabPane tabId='teamleave'>
          <Leave tabId={active} selectedUser={selectedUser} />
        </TabPane>
      </TabContent>
    </Fragment>
  )
}
export default UserTabs
