// ** React Imports
import { Fragment, useEffect, useState } from 'react'
import axios from '@src/configs/axios/axiosConfig'
// ** Reactstrap Imports
import {
  Row,
  Col,
  Card,
  Label,
  Input,
  Table,
  CardBody
} from 'reactstrap'

const Permissions = ({ roleId, tabId }) => {

  const [modules, setModules] = useState([])
  const [permissions, setPermissions] = useState([])

  const getModules = () => {
    axios.post('/modules/list').
      then((res) => {
        setModules(res.data.modules)
      }).catch(() => { })
  }

  const getRolePermissions = () => {
    axios.post('/rolepermissions/list', { roleId }).
      then((res) => {
        setPermissions(res.data.rolepermission.permissions.split(','))
      }).catch(() => { })
  }

  useEffect(() => {
    if (tabId === 'teammypermissions') {
      setModules([])
      getRolePermissions()
      getModules()
    }
  }, [roleId, tabId])

  return (
    <Fragment>
      <Card>
        <CardBody className='p-2'>
          <Row >
            <Col xs={12}>
              <h4 className='mt-2 pt-50'>Role Permissions</h4>
              <Table className='table-flush-spacing' responsive>
                <tbody>{
                  permissions.length > 0 &&
                  modules.map((module, index) => {
                    return (
                      <tr key={index}>
                        <td className='text-nowrap fw-bolder'>{module.name}</td>
                        <td>
                          <div className='d-flex'>
                            <div className='form-check me-3 me-lg-5'>
                              <Input type='checkbox' id={`read-${module.slug}`} value={`${module.slug}-read`} defaultChecked={permissions.indexOf(`${module.slug}-read`) > -1} disabled />
                              <Label className='form-check-label' for={`read-${module.slug}`}>
                                Read
                              </Label>
                            </div>
                            <div className='form-check me-3 me-lg-5'>
                              <Input type='checkbox' id={`write-${module.slug}`} value={`${module.slug}-write`} defaultChecked={permissions.indexOf(`${module.slug}-write`) > -1} disabled />
                              <Label className='form-check-label' for={`write-${module.slug}`}>
                                Write
                              </Label>
                            </div>
                            <div className='form-check me-3 me-lg-5'>
                              <Input type='checkbox' id={`delete-${module.slug}`} value={`${module.slug}-delete`} defaultChecked={permissions.indexOf(`${module.slug}-delete`) > -1} disabled />
                              <Label className='form-check-label' for={`delete-${module.slug}`}>
                                Delete
                              </Label>
                            </div>
                            <div className='form-check me-3 me-lg-5'>
                              <Input type='checkbox' id={`import-${module.slug}`} value={`${module.slug}-import`} defaultChecked={permissions.indexOf(`${module.slug}-import`) > -1} disabled />
                              <Label className='form-check-label' for={`import-${module.slug}`}>
                                import
                              </Label>
                            </div>
                            <div className='form-check'>
                              <Input type='checkbox' id={`export-${module.slug}`} value={`${module.slug}-export`} defaultChecked={permissions.indexOf(`${module.slug}-export`) > -1} disabled />
                              <Label className='form-check-label' for={`export-${module.slug}`}>
                                Export
                              </Label>
                            </div>
                          </div>
                        </td>
                      </tr>
                    )
                  })
                }
                </tbody>
              </Table>
            </Col>
          </Row>
        </CardBody>
      </Card>
    </Fragment>
  )
}

export default Permissions